import axios from '@nextcloud/axios';
import OIDCItem from './components/OIDCItem.vue';
import RedirectItem from './components/RedirectItem.vue';
import { generateUrl } from '@nextcloud/router';
export default {
  name: 'App',
  components: {
    OIDCItem,
    RedirectItem
  },
  props: {
    clients: {
      type: Array,
      required: true
    },
    expireTime: {
      type: String,
      required: true
    },
    publicKey: {
      type: String,
      required: true
    },
    groups: {
      type: Array,
      required: true
    },
    logoutRedirectUris: {
      type: Array,
      required: true
    },
    integrateAvatar: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      newClient: {
        name: '',
        redirectUri: '',
        signingAlg: 'RS256',
        type: 'confidential',
        errorMsg: '',
        error: false
      },
      newLogoutRedirectUri: {
        redirectUri: ''
      },
      expTime: this.expireTime,
      intAvatar: this.integrateAvatar,
      error: false,
      errorMsg: '',
      version: 0
    };
  },
  methods: {
    deleteRedirectUri(id) {
      axios.delete(generateUrl('apps/oidc/clients/redirect/{id}', {
        id
      })).then(response => {
        // eslint-disable-next-line vue/no-mutating-props
        this.clients.splice(0, this.clients.length);
        for (let index = 0; index < response.data.length; index++) {
          // eslint-disable-next-line vue/no-mutating-props
          this.clients.push(response.data[index]);
        }
        this.version += 1;
      }).catch(reason => {
        this.error = true;
        this.errorMsg = reason;
      });
    },
    addRedirectUri(id, uri) {
      this.error = false;
      axios.post(generateUrl('apps/oidc/clients/redirect'), {
        id,
        redirectUri: uri
      }).then(response => {
        // eslint-disable-next-line vue/no-mutating-props
        this.clients.splice(0, this.clients.length);
        for (let index = 0; index < response.data.length; index++) {
          // eslint-disable-next-line vue/no-mutating-props
          this.clients.push(response.data[index]);
        }
        this.version += 1;
      }).catch(reason => {
        this.error = true;
        this.errorMsg = reason;
      });
    },
    deleteLogoutRedirectUri(id) {
      axios.delete(generateUrl('apps/oidc/logoutRedirect/{id}', {
        id
      })).then(response => {
        // eslint-disable-next-line vue/no-mutating-props
        this.logoutRedirectUris = response.data;
        this.version += 1;
      }).catch(reason => {
        this.error = true;
        this.errorMsg = reason;
      });
    },
    addLogoutRedirectUri() {
      this.error = false;
      axios.post(generateUrl('apps/oidc/logoutRedirect'), {
        redirectUri: this.newLogoutRedirectUri.redirectUri
      }).then(response => {
        // eslint-disable-next-line vue/no-mutating-props
        this.logoutRedirectUris = response.data;
        this.newLogoutRedirectUri.redirectUri = '';
        this.version += 1;
      }).catch(reason => {
        this.error = true;
        this.errorMsg = reason;
      });
    },
    deleteClient(id) {
      axios.delete(generateUrl('apps/oidc/clients/{id}', {
        id
      })).then(response => {
        // eslint-disable-next-line vue/no-mutating-props
        this.clients = this.clients.filter(client => client.id !== id);
      });
    },
    addClient() {
      this.newClient.error = false;
      axios.post(generateUrl('apps/oidc/clients'), {
        name: this.newClient.name,
        redirectUri: this.newClient.redirectUri,
        signingAlg: this.newClient.signingAlg,
        type: this.newClient.type
      }).then(response => {
        // eslint-disable-next-line vue/no-mutating-props
        this.clients.push(response.data);
        this.newClient.name = '';
        this.newClient.redirectUri = '';
        this.newClient.signingAlg = 'RS256';
        this.newClient.type = 'confidential';
      }).catch(reason => {
        this.newClient.error = true;
        this.newClient.errorMsg = reason.response.data.message;
      });
    },
    setTokenExpireTime() {
      axios.post(generateUrl('apps/oidc/expire'), {
        expireTime: this.expTime
      }).then(response => {
        // eslint-disable-next-line vue/no-mutating-props
        this.expTime = response.data.expire_time;
        // eslint-disable-next-line vue/no-mutating-props
        this.expireTime = response.data.expire_time;
      });
    },
    setIntegrateAvatar() {
      axios.post(generateUrl('apps/oidc/integrateAvatar'), {
        integrateAvatar: this.intAvatar
      }).then(response => {
        // eslint-disable-next-line vue/no-mutating-props
        this.intAvatarDataUrl = response.data.integrate_avatar;
        // eslint-disable-next-line vue/no-mutating-props
        this.integrateAvatarDataUrl = response.data.integrate_avatar;
      });
    },
    regenerateKeys() {
      axios.post(generateUrl('apps/oidc/genKeys'), {}).then(response => {
        // eslint-disable-next-line vue/no-mutating-props
        this.publicKey = response.data.public_key;
      });
    },
    updateGroups(id, groups) {
      this.error = false;
      axios.patch(generateUrl('apps/oidc/clients/groups/{id}', {
        id
      }), {
        id,
        groups
      }).then(response => {
        // Nothing to do
      }).catch(reason => {
        this.error = true;
        this.errorMsg = reason;
      });
    },
    updateFlowTypes(id, flowTypes) {
      this.error = false;
      let resultingFlowTypes = 'code';
      if (flowTypes && flowTypes.value === 'code id_token') {
        resultingFlowTypes = 'code id_token';
      }
      axios.patch(generateUrl('apps/oidc/clients/flows/{id}', {
        id
      }), {
        id,
        flowType: resultingFlowTypes
      }).then(response => {
        // Nothing to do
      }).catch(reason => {
        this.error = true;
        this.errorMsg = reason;
      });
    }
  }
};